import React, { useState } from "react";
import './Home.css';
import { Outlet } from "react-router-dom";
import Tags from "../components/Tags";
import { Container } from "@mui/system";
import Sorts from "../components/Sorts";
import DrawingContainer from "../components/DrawingContainer";

const Sort = {
  HEART: { name: "", apiPath: "heart" },
  RECENT: { name: "", apiPath: "recent" },
  RANDOM: { name: "", apiPath: "random" },
};
Object.freeze(Sort);

const Tag = [
  { name: "", tagId: 1 },
  { name: "", tagId: 2 },
  { name: "", tagId: 3 },
  { name: "", tagId: 4 },
  { name: "", tagId: 5 },
  { name: "", tagId: 6 },
  { name: "", tagId: 7 },
];
Object.freeze(Tag);

const Tag2 = [
  { name: " ", tagId: 8 },
  { name: " ", tagId: 9 },
  { name: " ", tagId: 10 },
  { name: "", tagId: 11 },
  { name: "", tagId: 12 },
  { name: "", tagId: 13 },
  { name: "", tagId: 14 },
  { name: "", tagId: 15 },
];
Object.freeze(Tag2);

function Home() {
  const [sort, setSort] = useState(Sort.HEART.apiPath);
  const sortChanged = (e) => {
    const sort = e.currentTarget.value;
    setSort(sort);
  }

  const [tagFilter, setTagFilter] = useState([]);
  const tagChanged = (e) => {
    const isAdding = e.target.checked;
    const tagId = e.target.value;
    if (isAdding) {
      if (tagFilter.indexOf(tagId) === -1)
        setTagFilter([...tagFilter, tagId]);
    }
    else {
      setTagFilter(tagFilter.filter(id => id !== tagId));
    }
  }


  const outletContext = { home: true };
  return (
    <div className="homecontainer">
      <div className="tagOptionBox">
        <div> <img src="/img/textLogo.png" alt="" /> </div>
        <p className="tagGuide">      </p>
        <div className="tagBox">
          <Tags tags={Tag} tagChanged={tagChanged}></Tags>
        </div>
        <div className="tagBox">
          <Tags tags={Tag2} tagChanged={tagChanged}></Tags>
        </div>
      </div>

      <Container maxWidth="xl">
        <div className="viewOptionBox">
          <button className="refresh" onClick={() => setTagFilter(prev => [...prev])}>   </button>
          <Sorts sorts={Sort} sortChanged={sortChanged} defaultSort={sort} />
        </div>
        <DrawingContainer sort={sort} tagFilter={tagFilter} />
      </Container>

      <Outlet context={outletContext} />
    </div>
  );
}

export default Home;